import {Component, Input, OnInit, TemplateRef, ViewEncapsulation} from '@angular/core';
import {BadgeVariantType} from '../badge/badge.model';

@Component({
  // eslint-disable-next-line @angular-eslint/no-host-metadata-property
  host: {
    '[class.tab-pane]': 'true',
    '[class.active]': 'isActive',
    '[attr.role]': '"tabpanel"'
  },
  selector: 'adapt-tab-panel, div[tab-panel]',
  template: '<ng-content *ngIf="renderContentWhenInactive || isActive"></ng-content>',
  encapsulation: ViewEncapsulation.None
})


export class AdaptTabsPanelComponent implements OnInit {

  _badge: string | number;
  kebabMenuPresent: boolean = false;

  @Input() isActive: boolean = false;
  // eslint-disable-next-line @angular-eslint/no-input-rename
  @Input('badge-type') badgeType: BadgeVariantType = 'secondary';
  @Input() animateBadge: boolean = false;
  @Input() showBadgeAlert: boolean = false;
  @Input() badgeAlertVariant: BadgeVariantType = 'info';
  @Input() badgeCustomClass: string = '';

  // eslint-disable-next-line @angular-eslint/no-input-rename
  @Input('adapt-tab-title') title: string;
  @Input() disabled: boolean;
  @Input() isHidden: boolean = false;
  @Input() icon: string;
  @Input() subtext: string;
  // eslint-disable-next-line @angular-eslint/no-input-rename
  @Input('icon-right') iconRight: string;
  // eslint-disable-next-line @angular-eslint/no-input-rename
  @Input('icon-close') iconClose: boolean = false;
  @Input('aria-label') ariaLabel: string;
  @Input('aria-labelledby') ariaLabelledby: string;
  @Input() kebabMenu: TemplateRef<void>;
  @Input() id: string;

  /**
   * When `false` the content will not be rendered in the html, but to avoid rendering at the
   * angular side need to make this:
   * <adapt-tab-panel #tab [renderContentWhenInactive]="false">
   *   <ng-template [ngIf]="tab.isActive">...</ng-template>
   * </adapt-tab-panel>
   */
  @Input() renderContentWhenInactive: boolean = true;

  @Input('badge')
  get badge(): string | number {
    return this._badge;
  }

  set badge(value: string | number) {
    if (value !== this._badge) {
      this._badge = value;
    }
  }

  ngOnInit(): void {
    this.kebabMenuPresent = !!this.kebabMenu;
  }
}
